type Timezone = { id: string; label: string; abbreviation: string; value: string; offset: number }

const abbreviation = new Date().toLocaleTimeString("en-us", { timeZoneName: "short" }).split(" ")[2]
const label = new Date().toLocaleTimeString("en-us", { timeZoneName: "long" }).split(" ")[2]
const value = Intl.DateTimeFormat().resolvedOptions().timeZone

const USER_TIMEZONE: Timezone = {
  id: "user",
  label: `${value} ${label} Time`,
  offset: new Date().getTimezoneOffset() * 60 * 1000,
  abbreviation: abbreviation,
  value,
}

export { USER_TIMEZONE }
export type { Timezone }
